body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Raleway", "Helvetica Neue", "Montserrat",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.component-body-container {
    padding: 20px;
}

.custom-btn {
    padding: 5px;
    border: none;
    margin-right: 5px;
    text-transform: uppercase;
    width: 60px;
    font-size: 0.8rem;
    cursor: pointer;
    outline: none;
}

.add-btn {
    background: #f7d360f8;
    color: #fff;
}

.header {
    text-align: left;
    padding: 20px;
    background: rgb(21, 76, 112);
    color: rgb(214, 212, 212);
    text-transform: uppercase;
}
.grid-container {
    display: grid;
    grid-template-columns: 40% 30% auto;
    margin-bottom: 10px;
}

.heading-container {
    margin: 30px 0 30px 0;
    width: 100%;
}
  
.grid-item {
    background-color: rgba(250, 252, 253, 0.274);
    font-size: 1.15rem;
    word-wrap: break-word;
    padding-right: 10px;
}

.name-heading, .phone-heading {
    font-size: 1rem;
    font-weight: 600;
    color: #808080;
    text-transform: uppercase;
}

.delete-btn {
    background: #f70101b0;
    color: #000;
}

.subscriber-form {
    margin-top: 30px;
}

.label-control {
    color: #808080;
    font-size: 1.15rem;
    margin-right: 15px;
}

.input-control {
    width: 300px;
    min-width: 100px;
    padding: 10px 15px;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 5px;
    box-sizing: border-box;
    font-size: 0.9rem;
    outline: none;
}

.subscriber-info-container {
    font-size: 1rem;
    margin-right: 10px;
    margin-bottom: 20px;
}

.subscriber-to-add-heading {
    color: #0a4f75;
    font-weight: 600;
}

.subscriber-info {
    color: #808080;
} 

.back-btn {
    background: #4d4c4cf8;
    color: #fff;
}
